import React, { useState } from "react";

import Visibility from "@mui/icons-material/Visibility";
import VisibilityOff from "@mui/icons-material/VisibilityOff";
import Box from "@mui/material/Box";
import CardContent from "@mui/material/CardContent";
import Checkbox from "@mui/material/Checkbox";
import FormControl from "@mui/material/FormControl";
import FormControlLabel from "@mui/material/FormControlLabel";
import FormGroup from "@mui/material/FormGroup";
import FormHelperText from "@mui/material/FormHelperText";
import Grid from "@mui/material/Grid";
import IconButton from "@mui/material/IconButton";
import InputAdornment from "@mui/material/InputAdornment";
import InputLabel from "@mui/material/InputLabel";
import OutlinedInput from "@mui/material/OutlinedInput";
import Paper from "@mui/material/Paper";
import Stack from "@mui/material/Stack";
import { useTheme } from "@mui/material/styles";
import TextField from "@mui/material/TextField";
import Typography from "@mui/material/Typography";

import { Formik } from "formik";
import { get } from "lodash";
import { useCookies } from "react-cookie";
import { useGoogleReCaptcha } from "react-google-recaptcha-v3";
import { useDispatch, useSelector } from "react-redux";
import { Link, useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import * as Yup from "yup";


import {
  getMeInfo,
  setAuthToken,
  setIsLoggedIn
} from "../../../store/slice/auth/authentication/authSlice";
import { postSignin } from "../../../store/slice/auth/signin/signinSlice";
import {
  expireTime,
  getCustomCookie,
  setCustomCookie
} from "../../hooks/cookie";
import translate from "../../hooks/translate";
import CMLoadingButton from "../../ui-components/buttons/CMLoadingButton";
import GoogleSignup from "../../../pages/auth/signin/GoogleSignup";
import FacebookSignup from "../../../pages/auth/signin/FacebookSignup";
import { GoogleReCaptchaProvider } from "react-google-recaptcha-v3";


const LoginForm = () => {

  const GOOGLE_RECAPTCHA_KEY = process.env.REACT_APP_GOOGLE_RECAPTCHA_KEY;
  const [showPassword, setShowPassword] = useState(false);
  const loading = useSelector((state) => state.signin.loading);

  const [, setCookie] = useCookies();
  const { executeRecaptcha } = useGoogleReCaptcha();

  const navigate = useNavigate();
  const dispatch = useDispatch();
  const theme = useTheme();

  const schemaObj = Yup.object().shape({
    email: Yup.string()
      .email(translate("common_error_email_valid_email"))
      .required(translate("common_error_email_required")),
    password: Yup.string()
      .min(6, translate("common_error_password_too_short_min_6_length"))
      .max(18, translate("common_error_password_too_long_max_18_length"))
      .required(translate("common_error_password_required")),
  });

  const cookieData = getCustomCookie("remember_me");

  const handleClickShowPassword = () => {
    setShowPassword(!showPassword);
  };

  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };

  const handleSignin = async (data) => {
    if (!executeRecaptcha) {
      toast.error("You are not authorized");
      return;
    }
    const token = await executeRecaptcha("yourAction");

    const payload = {
      email: data.email,
      password: data.password,
      googleRecaptchaToken: token,
    };
    const res = await dispatch(postSignin(payload))
      .unwrap()
      .then(async (result) => {
        if (result.status && get(result, "data.token", "") !== "") {
          const userData = {
            token: result.data.token,
          };
          setCookie("consultant_data", userData, {
            path: "/",
            expires: expireTime,
          });

          await dispatch(getMeInfo())
            .unwrap()
            .then((result) => {
              if (result.status) {
                setCookie(
                  "isSignupCompleted",
                  get(result, "data.termsAndCondition", false),
                  { path: "/", expires: expireTime }
                );
              }
            })
            .catch((error) => {
              console.log("me error=======>", error);
            });

          dispatch(setAuthToken(result.data.token));
          dispatch(setIsLoggedIn(true));
          toast.success(result.message);
          navigate("/dashboard");
        } else {
          toast.error(result.message);
        }
      })
      .catch((error) => {
        console.log("signin error========>", error);
      });
    return res;
  };

  return (
    <>
      <Paper variant="outlined">
        <CardContent>
          <Formik
            initialValues={{
              email: get(cookieData, "email", ""),
              password: "",
              rememberMe: false,
            }}
            validationSchema={schemaObj}
            onSubmit={(values) => {
              handleSignin(values);
              if (values.rememberMe) {
                const rememberMeData = {
                  email: values.email,
                };
                setCustomCookie("remember_me", rememberMeData);
              }
            }}
          >
            {(props) => (
              <form onSubmit={props.handleSubmit}>
                <Typography variant="h4" align="center" mb={3}>
                  {translate("consultant_get_ready_to_login")}
                </Typography>
                <Stack
                    direction="row"
                    spacing={3}
                    justifyContent="center"
                    mb={3}
                  >
                    
                    <GoogleSignup />
                    <FacebookSignup />
                  </Stack>
                <Grid container spacing={2} justifyContent="center">
                  <Grid item xs={12}>
                    <TextField
                      label={translate("common_email")}
                      name="email"
                      value={props.values.email}
                      onChange={props.handleChange}
                      onBlur={props.handleBlur}
                      // fullWidth
                      error={
                        props.errors.email && props.touched.email ? true : false
                      }
                      helperText={
                        props.errors.email && props.touched.email
                          ? props.errors.email
                          : null
                      }
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <FormControl
                      variant="outlined"
                      fullWidth
                      sx={{ ...theme.typography.customInput }}
                    >
                      <InputLabel
                        error={props.errors.password && props.touched.password}
                        htmlFor="outlined-adornment-password"
                      >
                        {translate("common_password")}
                      </InputLabel>
                      <OutlinedInput
                        id="outlined-adornment-password"
                        label={translate("common_password")}
                        type={showPassword ? "text" : "password"}
                        name="password"
                        value={props.values.password}
                        onChange={props.handleChange}
                        onBlur={props.handleBlur}
                        endAdornment={
                          <InputAdornment position="end">
                            <IconButton
                              aria-label="toggle password visibility"
                              onClick={handleClickShowPassword}
                              onMouseDown={handleMouseDownPassword}
                              edge="end"
                            >
                              {showPassword ? (
                                <VisibilityOff />
                              ) : (
                                <Visibility />
                              )}
                            </IconButton>
                          </InputAdornment>
                        }
                        error={
                          props.errors.password && props.touched.password
                            ? true
                            : false
                        }
                      />
                      {props.errors.password && props.touched.password && (
                        <FormHelperText error id="outlined-adornment-password">
                          {props.errors.password}
                        </FormHelperText>
                      )}
                    </FormControl>
                    <Stack
                      direction={{ xs: "column", sm: "row" }}
                      alignItems={{ xs: "start", sm: "center" }}
                      justifyContent={{ xs: "center", sm: "space-between" }}
                      sx={{ width: "100%" }}
                    >
                      <Box component="div">
                        <FormGroup>
                          <FormControlLabel
                            control={
                              <Checkbox
                                name="rememberMe"
                                value={props.values.rememberMe}
                                onChange={props.handleChange}
                              />
                            }
                            label={translate("common_remember_me") + " ?"}
                          />
                        </FormGroup>
                      </Box>
                      <Box component="div">
                        <Link to="/recover/account">
                          {translate("common_forgot_password")} ?
                        </Link>
                      </Box>
                    </Stack>
                  </Grid>

                  <Grid item xs={12}>
                    <CMLoadingButton
                      variant="contained"
                      fullWidth
                      type="submit"
                      loading={props.isSubmitting && loading}
                      disabled={props.isSubmitting && loading}
                    >
                      {translate("common_sign_in")}
                    </CMLoadingButton>
                  </Grid>
                  <Typography variant="body1" mt={2}>
                    {translate("common_dont_have_an_account")} ?{" "}
                    <Link to="/signup">{translate("common_sign_up")}</Link>
                  </Typography>
                </Grid>
              </form>
            )}
          </Formik>
        </CardContent>
      </Paper>
    </>
  );
};

export default LoginForm;
