import IconButton from "@mui/material/IconButton";
import { alpha, styled } from "@mui/material/styles";
import { get } from "lodash";
import React from "react";
import { useCookies } from "react-cookie";
import FacebookLogin from "react-facebook-login/dist/facebook-login-render-props";
import { useGoogleReCaptcha } from "react-google-recaptcha-v3";
import { useDispatch } from "react-redux";
import { toast } from "react-toastify";
import { FaceBookIcon } from "../../../components/Icon";
import ComponentStyle from "../../../styles/ComponentStyle.module.css";
import {
  getMeInfo,
  setAuthToken,
  setIsLoggedIn,
} from "../../../store/slice/auth/authentication/authSlice";
import { postSignin } from "../../../store/slice/auth/signin/signinSlice";
import {
  expireTime,
  getCustomCookie,
  setCustomCookie,
} from "../../../components/hooks/cookie";
import { Link, useNavigate } from "react-router-dom";


const CMIconButton = styled(IconButton)(({ theme }) => ({
  border: `1px solid ${alpha(theme.palette.grey[400], 0.3)}`,
}));

const FacebookSignup = () => {
  const { executeRecaptcha } = useGoogleReCaptcha();
  const [, setCookie] = useCookies();
  const dispatch = useDispatch();
  const navigate = useNavigate();


  const FACEBOOK_ID = process.env.REACT_APP_FACEBOOK_ID;

  const responseFacebook = async (response) => {
    if (!executeRecaptcha) {
      toast.error("You are not authorized");
      return;
    }
    handleSignin(response);
  };


  const handleSignin = async (data) => {
    if (!executeRecaptcha) {
      toast.error("You are not authorized");
      return;
    }
    const token = await executeRecaptcha("yourAction");

    const payload = {
      email: data.email,
      firstName: data.name,
      lastName:'',
      profilePictureURL: data.picture.data.url,
      googleRecaptchaToken: token,
      provider: data.graphDomain,
      password: "",
      
    };
    const res = await dispatch(postSignin(payload))
      .unwrap()
      .then(async (result) => {
        if (result.status && get(result, "data.token", "") !== "") {
          const userData = {
            token: result.data.token,
          };
          setCookie("consultant_data", userData, {
            path: "/",
            expires: expireTime,
          });

          await dispatch(getMeInfo())
            .unwrap()
            .then((result) => {
              if (result.status) {
                setCookie(
                  "isSignupCompleted",
                  get(result, "data.termsAndCondition", false),
                  { path: "/", expires: expireTime }
                );
              }
            })
            .catch((error) => {
              console.log("me error=======>", error);
            });

          dispatch(setAuthToken(result.data.token));
          dispatch(setIsLoggedIn(true));
          toast.success(result.message);
          navigate("/dashboard");
        } else {
          toast.error(result.message);
        }
      })
      .catch((error) => {
        console.log("signin error========>", error);
      });
    return res;
  };

  const handleFailure = (err) => {
    console.log("facebook Error", err);
  };

  return (
    <>
      <FacebookLogin
        appId={415907740177970}
        fields="name,email,picture"
        scope="public_profile,email"
        render={(renderProps) => (
          <CMIconButton onClick={renderProps.onClick}>
            <FaceBookIcon className={ComponentStyle.social_icon_btn} />
          </CMIconButton>
        )}
        callback={responseFacebook}
        onFailure={handleFailure}
      />
    </>
  );
};

export default FacebookSignup;
