import React, { useEffect } from "react";

import IconButton from "@mui/material/IconButton";
import { alpha, styled } from "@mui/material/styles";

import { gapi } from "gapi-script";
import { GoogleLogin } from "react-google-login";

import { GoogleIcon } from "../../../components/Icon";
import ComponentStyle from "../../../styles/ComponentStyle.module.css";
import { get } from "lodash";
import { useCookies } from "react-cookie";
import { useGoogleReCaptcha } from "react-google-recaptcha-v3";
import { useDispatch, useSelector } from "react-redux";
import { Link, useNavigate } from "react-router-dom";
import {
  getMeInfo,
  setAuthToken,
  setIsLoggedIn,
} from "../../../store/slice/auth/authentication/authSlice";
import { postSignin } from "../../../store/slice/auth/signin/signinSlice";
import {
  expireTime,
  getCustomCookie,
  setCustomCookie,
} from "../../../components/hooks/cookie";
import translate from "../../../components/hooks/translate";
import { toast } from "react-toastify";

const CMIconButton = styled(IconButton)(({ theme }) => ({
  border: `1px solid ${alpha(theme.palette.grey[400], 0.3)}`,
}));

const GoogleSignup = () => {
  const CLIENT_ID = process.env.REACT_APP_GOOGLE_CLIENT_ID;
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const loading = useSelector((state) => state.signin.loading);
  const signin = useSelector((state) => state.auth.isLoggedIn);

  const [, setCookie] = useCookies();
  const { executeRecaptcha } = useGoogleReCaptcha();

  useEffect(() => {
    function start() {
      gapi.client.init({
        clientId: CLIENT_ID,
        scope: "openid profile email",
      });
    }
    gapi.load("client:auth2", start);
  }, [CLIENT_ID]);

  const onLoginSuccess = (response) => {
  };

  const onFailureSuccess = (response) => {
  };
  const handelClick = (response) => {
  };
  const handleLogin = (googleData) => {
    handleSignin(googleData);
  };

  const handleSignin = async (data) => {
    if (!executeRecaptcha) {
      toast.error("You are not authorized");
      return;
    }
    const token = await executeRecaptcha("yourAction");

    const payload = {
      email: data.profileObj.email,
      firstName: data.profileObj.givenName,
      lastName: data.profileObj.familyName,
      profilePictureURL: data.profileObj.imageUrl,
      googleRecaptchaToken: token,
      provider: data.tokenObj.idpId,
      password: "",
    };
    const res = await dispatch(postSignin(payload))
      .unwrap()
      .then(async (result) => {
        if (result.status && get(result, "data.token", "") !== "") {
          const userData = {
            token: result.data.token,
          };
          setCookie("consultant_data", userData, {
            path: "/",
            expires: expireTime,
          });

          await dispatch(getMeInfo())
            .unwrap()
            .then((result) => {
              if (result.status) {
                setCookie(
                  "isSignupCompleted",
                  get(result, "data.termsAndCondition", false),
                  { path: "/", expires: expireTime }
                );
              }
            })
            .catch((error) => {
              console.log("me error=======>", error);
            });

          dispatch(setAuthToken(result.data.token));
          dispatch(setIsLoggedIn(true));
          toast.success(result.message);
          navigate("/dashboard");
        } else {
          toast.error(result.message);
        }
      })
      .catch((error) => {
        console.log("signin error========>", error);
      });
    return res;
  };
  return (
    <>
      <GoogleLogin
        clientId="1037386275248-r06qjdrbebf3k49hl5n4bs63rskmrrel.apps.googleusercontent.com"
        onSuccess={handleLogin}
        onFailure={handleLogin}
        isSignedIn={signin}
        autoLoad={false}
        render={(renderProps) => (
          <CMIconButton onClick={renderProps.onClick}>
            <GoogleIcon className={ComponentStyle.social_icon_btn} />
          </CMIconButton>
        )}
        // onClick={handleLogin}
        cookiePolicy={"single_host_origin"}
        scope="profile email"
      />
    </>
  );
};

export default GoogleSignup;
